}

.banner-area .banner-content h1 {
  font-weight: 600;
  font-size: 58px;
  color: #ffffff;
  margin-bottom: 25px;
}

.banner-area .banner-content p {
  font-weight: 500;
  font-size: 19px;
  margin-bottom: 30px;
  color: #ffffff;
}

.banner-area.banner-area-video {
  overflow: hidden;
  z-index: 1;
}

.banner-area.banner-area-video .background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
  z-index: -2;
}

.banner-area.banner-area-video .d-table {
  position: relative;
  z-index: 3;
}

.cmn-btn {
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  color: #fa5f0b;
  background-color: #ffffff;
  border-radius: 5px;
  padding: 12px 20px;
  position: relative;
  z-index: 1;
}

.cmn-btn:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 0%;
  left: 0;
  top: 0;
  background-color: #282828;
  border-radius: 5px 5px 0 0;
  transition: 0.5s all ease;
  opacity: 0;
  z-index: -1;
}

.cmn-btn:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0%;
  left: 0;
  bottom: 0;
  background-color: #282828;
  border-radius: 0 0 5px 5px;
  transition: 0.5s all ease;
  opacity: 0;
  z-index: -1;
}

.cmn-btn i {
  display: inline-block;
  font-size: 18px;
  position: relative;
  top: 3px;
}

.cmn-btn:hover {
  color: #ffffff;
}

.cmn-btn:hover:before {
  opacity: 1;
  height: 50%;
}

.cmn-btn:hover:after {
  opacity: 1;
  height: 50%;
}

/*-- End Banner --*/
/*-- About --*/
.section-title {
  margin-bottom: 35px;
  text-align: center;
}

.section-title .sub-title {
  display: inline-block;
  color: #fa5f0b;
  font-weight: 500;
  font-size: 17px;
  margin-bottom: 8px;
}

.section-title h2 {
  font-weight: 600;
  font-size: 38px;
  color: #282828;
  text-transform: capitalize;
}

.about-area {
  padding-top: 165px;
  padding-bottom: 155px;
}

.about-area .section-title {
  text-align: left;
  margin-bottom: 30px;
}

.about-area .about-content p {
  color: #282828;
  font-weight: 500;
  margin-bottom: 18px;
}

.about-area .about-content ul {
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
}

.about-area .about-content ul li {
  list-style-type: none;
  display: block;
  font-weight: 500;
  margin-bottom: 15px;
}

.about-area .about-content ul li:last-child {
