}

.about-area .about-img-wrap {
  position: relative;
}

.about-area .about-img-wrap .about-img-slider .about-img-item img {
  width: unset !important;
  max-width: 450px;
}

.about-area .about-img-wrap .owl-theme .owl-nav.disabled+.owl-dots {
