}

.about-area .about-img-wrap .owl-theme .owl-dots .owl-dot.active span {
  background: #ffffff;
}

.about-area .about-img-wrap .about-shape img {
  position: absolute;
}

.about-area .about-img-wrap .about-shape img:nth-child(1) {
  top: -25px;
  left: 0;
}

.about-area .about-img-wrap .about-shape img:nth-child(2) {
  bottom: -55px;
  right: 0;
  z-index: 1;
}

.about-area .about-img-wrap .about-shape img:nth-child(3) {
  top: -65px;
  right: 25px;
}

.about-area .about-img-wrap .about-shape img:nth-child(4) {
  top: 255px;
  right: -50px;
}

.about-area .about-img-wrap .about-year {
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.8509803922);
  border-left: 5px solid #02111e;
  display: inline-block;
  padding: 35px 60px;
  position: absolute;
  left: 0;
  bottom: -36px;
}

.about-area .about-img-wrap .about-year h2 {
  font-size: 58px;
  color: #02111e;
  font-weight: 700;
  margin-bottom: 0;
}

.about-area .about-img-wrap .about-year h2 span {
  font-size: 38px;
}

/*-- End About --*/
/*-- Service --*/
.service-area {
  background-image: url("../img/111.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  background-attachment: fixed;
}

.service-area:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #010006;
  opacity: 0.6;
}

.service-area .section-title {
  position: relative;
}

.service-area .section-title h2 {
  color: #ffffff;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.service-area .section-title h2 span {
  color: #fa5f0b;
}

.service-item {
  text-align: center;
  padding: 30px 30px 28px;
  border-radius: 10px;
  background-color: #ffffff;
  position: relative;
  z-index: 1;
  transition: 0.5s all ease;
  margin-bottom: 30px;
}

.service-item:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 0%;
  bottom: 0;
  left: 0;
  background-color: #fa5f0b;
  transition: 0.5s all ease;
  border-radius: 10px;
  z-index: -1;
}

.service-item:hover {
  transform: translate(0, -10px);
}

.service-item:hover:before {
  height: 100%;
}

.service-item:hover img:nth-child(1) {
  opacity: 0;
}

.service-item:hover img:nth-child(2) {
  opacity: 0.5;
}

.service-item:hover i {
  border: 1px solid #fa5f0b;
}

.service-item:hover h3 a {
  color: #ffffff;
}

.service-item:hover p {
  color: #ffffff;
}

.service-item:hover .service-link {
  color: #ffffff;
}

.service-item img {
  position: absolute;
  top: 25px;
  left: 0;
  right: 0;
  z-index: -1;
  margin-left: auto;
  margin-right: auto;
  transition: 0.5s all ease;
}

.service-item img:nth-child(1) {
  opacity: 0.5;
}

.service-item img:nth-child(2) {
  opacity: 0;
}

.service-item i {
  display: inline-block;
  width: 70px;
  height: 70px;
  line-height: 70px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 1px dotted #fa5f0b;
  color: #fa5f0b;
  font-size: 35px;
  margin-bottom: 15px;
  transition: 0.5s all ease;
}

.service-item h3 {
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 8px;
  transition: 0.5s all ease;
}

.service-item h3 a {
  color: #282828;
}

.service-item h3 a:hover {
  color: #282828;
}

.service-item p {
  margin-bottom: 12px;
  transition: 0.5s all ease;
}

.service-item .service-link {
  display: inline-block;
  color: #fa5f0b;
  font-weight: 500;
  font-size: 14px;
}

.service-item .service-link:hover {
  color: #282828;
}

/*-- End Service --*/
